<template>
  <div class="interval">
    <div class="table-box">
            <el-input v-model="table.query.condition.workSegmentName"
                      @keyup.enter.native="getData()"
                      class="mr10" placeholder=""></el-input>
            <div @click="getData" class="btn-main ">&nbsp;&nbsp;&nbsp;</div>
      <div @click="showDialog('add')" class="btn-main btn-add mb20"></div>
      <table cellspacing="0"  class="table">
        <tr>
          <th width="10%"></th>
          <th width="20%"></th>
          <th width="35%"></th>
          <th width="15%"></th>
          <th width="20%"></th>
        </tr>
        <tr v-for="(item,key) in table.list">
          <td>{{(key + 1) + (table.query.currentPage - 1) * table.query.pageSize}}</td>
          <td>{{item.workSegmentName}}</td>
          <td>{{item.mileageSegmentName}}</td>
          <td>{{item.workDate}}</td>
          <td>
            <span @click="showDialog('edit',item)" class="oper"></span>
            <span class="col"></span>
            <span @click="remove(item)" class="oper"></span>
          </td>
        </tr>
      </table>
      <el-pagination
          class="pagination"
          :page-size="table.query.pageSize"
          @current-change="handleCurrentChange"
          :current-page.sync="table.query.currentPage"
          layout="prev, pager, next, jumper"
          :total="table.total">
      </el-pagination>
    </div>
    <el-dialog :modal="false" center :title="title"
               :visible.sync="dialog" width="590px">
      <div class="title-box">
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div class="content">
        <div>
          <input :value="name" disabled type="text"/>
        </div>
        <div>
          k <input v-model="value1" type="text"/>
          + <input  v-model="value2" type="text"/>
          - k <input v-model="value3" type="text"/>
          + <input v-model="value4" type="text"/>
        </div>
        <div>
          <input type="text" />
        </div>
      </div>
      <div class="btns mt25">
        <div  @click="editFun()" class="btn-main mr15"></div>
        <div @click="dialog = false" class="btn-main"></div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        value1: '',
        value2: '',
        value3: '',
        value4: '',
        table:{
          url: '/workSegment/list',
          loading: '.table',
          list: [],
          total: 0,
          query:{
            condition:{
              workSegmentName: '',
            },
            currentPage: 1,
            pageSize: this.$conf.PAGES,
          }
        },
        // 
        addEditConfig: {
          url: '/mileage/add',
          query:{
            startMileageName: '',
            endMileageName: '',
          }
        },
        visible: false,
        title: '',
        dialog: false,
      }
    },
    computed: {
      name: function () {
        if(this.value1 && this.value2 && this.value3){
          return `k${this.value1}+${this.value2} - k${this.value3}+${this.value4}`
        }

      }
    },
    methods: {
      // 
      showDialog(type, item){
        this.dialog = true
        if(item){
          this.title = ''
          this.addEditConfig.query = {
            id: item.id,
            startMileageName: item.startMileageName,
            endMileageName: item.endMileageName,
          }
        }else{
          this.title = ''
          this.addEditConfig.query = {
            startMileageName: '',
            endMileageName: '',
          }
        }
      },
      // 
      async getData(){
        let res = await this.$ajax(this.table)
        this.table.list = res.pageItems
        this.table.total = res.totalCount
      },
      // 
      async remove(item){
        await this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        await this.$ajax({
          url: '/mileage/delete',
          query:{
            id: item.id
          }
        })
        this.$remind('')
        this.getData()
      },
      // 
      async editFun(){
        let text = ''
        if(this.title === ''){
          text = ''
          this.addEditConfig.url = '/workSegment/update'
        }else{
          text = ''
          this.addEditConfig.url = '/workSegment/add'
        }
        if(!this.value1 || !this.value2 || !this.value3 || !this.value4){
          this.$message.error('')
        }
        this.addEditConfig.query.startMileageName = `${this.value1}+${this.value2}`
        this.addEditConfig.query.endMileageName = `${this.value3}+${this.value4}`

        await this.$ajax(this.addEditConfig)
        this.$remind(text)
        this.dialog = false
        this.table.query = {
          condition:{
            mileageName: '',
          },
          currentPage: 1,
          pageSize: this.$conf.PAGES,
        }
        this.getData()
      },
      // 
      handleCurrentChange(e){
        this.table.query.currentPage = e
        this.getData()
      }
    },
    mounted() {
      this.getData()
    }
  }
</script>

