      span:nth-of-type(2)
        width: 268px
      span:nth-of-type(3)
        width: 100px
    .content
      height: 40px
      line-height: 40px
      border-top: 1px solid #eee
      border-bottom: 1px solid #eee
      input
        height: 24px
        border: 1px solid $main-c
      div
        text-align: center
        display: inline-block
      div:nth-of-type(1)
        width: 170px
        input
          width: 170px
          cursor: no-drop
          background: #eee
          padding: 0 5px
      div:nth-of-type(2)
        width: 268px
        input
          width: 40px
      div:nth-of-type(3)
        width: 100px
        input
          width: 65px


</style>
